  #navigation-bar{
      position: fixed;
      height: 100px;
      width: 100%;
      z-index: 999;
      padding: 10px;
      background:transparent;
      overflow: hidden;
  }
  .menubar{
      position: absolute;
      right:4%;
      top: 40%;
      cursor: pointer;
      opacity: .8;
      transition: all .4s ease;
  }
  .menubar span{
      position: relative;
      background-color:transparent;
      height:2px;
      width: 20px;
      display: block;
      margin:6px;
      border-radius: 20px;
  }
  .menubar .first-span{
      width:35px;
  }
  .menubar .first-span::before{
      content: "";
      position: absolute;
      height: 100%;
      width: 50%;
      top:0;
      right:0;
      background-color:whitesmoke;
      transition: all .5s ease;
  
  }
  .menubar .second-span{
      width:35px;
  }
  .menubar .second-span::before{
      content: "";
      position: absolute;
      height: 100%;
      width: 75%;
      top:0;
      right:0;
      background-color:whitesmoke;
      transition: all .3s ease;
  }
  .menubar .third-span{
      width:35px;
  }
  .menubar .third-span::before{
      content: "";
      position: absolute;
      height: 100%;
      width: 100%;
      top:0;
      right:0;
      background-color:whitesmoke;
      transition: all .3s ease;
  }
  .menubar:hover .second-span::before , .menubar:hover .first-span::before {
      width: 100%;
  }
  .menubar:hover{
      opacity: 1;
  }
  .close-first , .close-second{
   height: 2px;
   width: 35px;
   background-color: whitesmoke;
   display: block;
   margin: 4px;
   cursor: pointer;
   padding: 1px;
   border-radius: 20px;
  }
  .close-first {
      transform: rotate(45deg);
  }
  .close-second{
      transform: rotate(-45deg);
  }
  .navigation-close{
      position: absolute;
      top: 6%;
      right:4%;
      padding: 10px;
      cursor: pointer;
      transition: all.3s ease;
      opacity: .8;
  }
  .navigation-close:hover{
      opacity: 1;
      transform: rotate(90deg);
  }
